import React, { useState, MouseEvent } from 'react';
import { alpha, Avatar, IconButton, Menu, MenuItem, Tooltip, Typography } from '@mui/material';
import { theme } from '../../../style/theme';
import { TOP_BAR_ACCOUNT_SETTINGS_ACCOUNT, TOP_BAR_ACCOUNT_SETTINGS_LOGOUT, TOP_BAR_ACCOUNT_TOOLTIP } from './constants';

export const TopBarAccount = () => {
    const [anchor, setAnchor] = useState<null | HTMLElement>(null);

    const onOpen = (e: MouseEvent<HTMLElement>) => {
        setAnchor(e.currentTarget);
    };

    const onClose = () => {
        setAnchor(null);
    };

    const onClick = (event: () => void) => {
        event();
        onClose();
    };

    const settings = [
        {
            title: TOP_BAR_ACCOUNT_SETTINGS_ACCOUNT,
            event: () => onClick(() => {})
        },
        {
            title: TOP_BAR_ACCOUNT_SETTINGS_LOGOUT,
            event: () => onClick(() => {})
        }
    ];

    return (
        <>
            <Tooltip
                title={TOP_BAR_ACCOUNT_TOOLTIP}
                PopperProps={{ sx: { pt: 2.75 } }}
                enterDelay={200}
                enterNextDelay={200}
            >
                <IconButton
                    onClick={onOpen}
                    sx={{ p: 0, height: theme.spacing(5) }}
                    disableRipple
                    disableTouchRipple
                    disableFocusRipple
                >
                    <Avatar alt={'Arkady'} src={'/static/images/avatar/2.jpg'} variant={'square'} sx={avatarStyle} />
                </IconButton>
            </Tooltip>
            <Menu
                anchorEl={anchor}
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'right',
                }}
                keepMounted
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                }}
                open={!!anchor}
                onClose={onClose}
                PaperProps={{ sx: menuStyle, elevation: 3 }}
            >
                {
                    settings.map(({ title, event  }) => (
                        <MenuItem
                            key={title}
                            onClick={event}
                            sx={{ height: theme.spacing(5.5), px: 3 }}
                        >
                            <Typography color={'secondary'}>
                                {title}
                            </Typography>
                        </MenuItem>
                    ))
                }
            </Menu>
        </>
    );
}

const menuStyle = {
    mt: 5,
    width: theme.spacing(24),
    borderRadius: theme.shape.borderRadius * 4,
    bgcolor: alpha(theme.palette.primary.light, 0.5)
};

const avatarStyle = {
    borderRadius: theme.shape.borderRadius * 2,
};